.page {
  width: 100%;
}

.page__container {
  margin: 0 auto;

  @media (max-width: $vp-768) {
    width: 84.38vw;
  }

  @media (min-width: $vp-768)  {
    width: 87.01vw;
  }

  @media (min-width: $vp-1024) {
    width: 63.65vw;
  }

  @media (min-width: $vp-1920) {
    max-width: 1222px;
  }
}

.page__full-logo {
  width: 215px;
  height: 55px;
  display: grid;
  grid-template-areas: "logo title" "logo description";
}

.page__logo-img {
  grid-area: logo;
}

.page__logo-title {
  grid-area: title;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: #F2F5F7;
  padding: 5px 0 0 2px;
  font-family: 'Roboto Condensed', Arial sans-serif;
  font-style: normal;
  margin: 0;
}

.page__logo-description {
  margin: 0;
  grid-area: description;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: rgba(255, 255, 255, 0.7);
  padding: 0px 0 0 2px;
}
